/*Option Select*/
.selectbox {
  position: relative;
  display: inline-block;
  vertical-align: middle;
}
.selectbox select {
  background-color: #c2b49a;
  color: #eee;
  font-size: inherit;
  padding: .5em;
  padding-right: 3em;
  min-width: 210px;
  border: 0;
  margin: 0;
  border-radius: 6px;
  -webkit-appearance: button;
  -moz-appearance: button;
  cursor: pointer;
}
.selectbox::before, .selectbox::after {
  content: "";
  position: absolute;
  pointer-events: none;
}
.selectbox::after {
  content: "\F0D7";
  font-family: "Font Awesome 5 Free";
  color: #eee;
  font-size: 1.3em;
  right: .5em;
  top: 50%;
  margin-top: -.5em;
}
.selectbox::before {
  width: 2em;
  right: 0;
  top: 0;
  bottom: 0;
  border-radius: 0 6px 6px 0;
  background-color: rgba(0, 0, 0, 0.2);
  -webkit-transition: all .3s ease-out;
  transition: all .3s ease-out;
}
.selectbox:hover::before {
  opacity: .8;
}
.selectbox select[disabled] {
  color: rgba(0, 0, 0, 0.3);
}
.selectbox select[disabled]::after {
  color: rgba(0, 0, 0, 0.1);
}
/*Number Input*/
input[type="number"] {
  position: relative;
  background: #c2b49a;
  border: none;
  color: #eee;
  border-radius: 6px;
  padding: .5em;
  min-width: 100px;
  -webkit-appearance: button;
  -moz-appearance: button;
}
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  background: rgba(0, 0, 0, 0.2) url(data:image/svg+xml;base64,) no-repeat center center;
  border-radius: 0 6px 6px 0;
  width: 1em;
  opacity: 1;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  -webkit-transition: all .3s ease-out;
  transition: all .3s ease-out;
  cursor: pointer;
  padding: 0 2px;
}
input[type="number"]::-webkit-inner-spin-button:hover, input[type="number"]::-webkit-inner-spin-button:active {
  opacity: .8;
}
/*Checkboxes and Radio Buttons*/
[type="checkbox"]:not(:checked), [type="checkbox"]:checked, [type="radio"]:not(:checked), [type="radio"]:checked {
  position: absolute;
  left: -9999px;
}
[type="checkbox"]:not(:checked) + label, [type="checkbox"]:checked + label, [type="radio"]:not(:checked) + label, [type="radio"]:checked + label {
  position: relative;
  padding-left: 35px;
  cursor: pointer;
  display: inline-block;
  text-transform: capitalize;
  font-size: 15px;
}
[type="checkbox"]:not(:checked) + label:before, [type="checkbox"]:checked + label:before, [type="checkbox"]:not(:checked) + label:after, [type="checkbox"]:checked + label:after, [type="radio"]:not(:checked) + label:before, [type="radio"]:checked + label:before, [type="radio"]:not(:checked) + label:after, [type="radio"]:checked + label:after {
  content: '';
  position: absolute;
}
[type="checkbox"]:not(:checked) + label:before, [type="checkbox"]:checked + label:before, [type="radio"]:not(:checked) + label:before, [type="radio"]:checked + label:before {
  left: 0;
  top: -4px;
  width: 25px;
  height: 25px;
  border: 2px solid #c2b49a;
  border-radius: 6px;
  -webkit-transition: background-color .2s;
  transition: background-color .2s;
}
[type="radio"]:not(:checked) + label:before, [type="radio"]:checked + label:before {
  border-radius: 100%;
}
[type="checkbox"]:checked + label:before, [type="radio"]:checked + label:before {
  background: #c2b49a;
  border: 2px solid #c2b49a;
}
[type="checkbox"]:checked + label:before, [type="checkbox"]:not(:checked) + label:before, [type="radio"]:checked + label:before, [type="radio"]:not(:checked) + label:before {
  content: "";
  display: inline-block;
  width: 25px;
  height: 25px;
  position: absolute;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 80%;
}
[type="checkbox"]:checked + label:before, [type="radio"]:checked + label:before {
  background-image: url(data:image/svg+xml;base64,);
}
[type="checkbox"]:checked + label:after, [type="radio"]:checked + label:after {
  border: 0;
  outline: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
/*Range Slider*/
input[type="range"] {
  -webkit-appearance: none;
  width: 100%;
  height: 5px;
  border-radius: 10em;
  background: #bbbbbb;
  outline: none;
  -webkit-box-shadow: inset 0 2px 5px rgba(0, 0, 0, 0.5);
          box-shadow: inset 0 2px 5px rgba(0, 0, 0, 0.5);
  -webkit-transition: all .2s;
  transition: all .2s;
}
input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #b3a181;
  cursor: pointer;
  border: 1px solid #ddd5c7;
  -webkit-box-shadow: 0 2px 5px rgba(0, 0, 0, 0.8);
          box-shadow: 0 2px 5px rgba(0, 0, 0, 0.8);
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
input[type="range"]:hover::-webkit-slider-thumb {
  background: #c2b49a;
}
input[type="range"]::-moz-range-thumb {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #c2b49a;
  cursor: pointer;
}
output {
  display: block;
  margin: 2em 0 0;
  width: 100%;
  background: #c2b49a;
  padding: .5em;
  width: 30%;
  border-radius: 6px;
