}
/*ToolTip*/
[tooltip-info] {
  position: relative;
  display: inline-block;
}
[tooltip-info]:after {
  content: attr(tooltip-info);
  display: inline-block;
  position: absolute;
  background: #444;
  min-width: 14vw;
  width: auto;
  color: #eee;
  text-align: center;
  margin: auto;
  bottom: 40px;
  left: -6.5vw;
  right: 0;
  border-radius: 5px;
  padding: .5em 1em;
  opacity: 0;
  z-index: 10;
  pointer-events: none;
  -webkit-transition: all .3s ease-out;
  transition: all .3s ease-out;
}
[tooltip-info]:before {
  content: '';
  width: 0;
  height: 0;
  opacity: 0;
  border-left: 15px solid transparent;
  border-right: 15px solid transparent;
  border-top: 15px solid #444;
  pointer-events: none;
  -webkit-transition: all .3s ease-out;
  transition: all .3s ease-out;
  position: absolute;
  z-index: 10;
  bottom: 25px;
  left: 165px;
  left: calc(50% - 15px);
}
[tooltip-info]:hover:before, [tooltip-info]:hover:after {
  opacity: 1;
  pointer-events: auto;
}
