/*Accordion*/
.v-collapse-group {
  background-color: rgba(255, 255, 255, 0.6);
  -webkit-box-shadow: 0 2px 3px rgba(10, 10, 10, 0.1), 0 0 0 1px rgba(10, 10, 10, 0.1);
          box-shadow: 0 2px 3px rgba(10, 10, 10, 0.1), 0 0 0 1px rgba(10, 10, 10, 0.1);
  max-width: 100%;
  position: relative;
  border-radius: .25rem;
}
h3.v-collapse-toggler {
  cursor: pointer;
  border-radius: .25rem;
  padding: .75rem 1.25rem;
  background-color: rgba(0, 0, 0, 0.08);
  border-bottom: 1px solid rgba(0, 0, 0, 0.125);
  -webkit-box-shadow: 0 1px 2px rgba(10, 10, 10, 0.1);
          box-shadow: 0 1px 2px rgba(10, 10, 10, 0.1);
  -webkit-transition: background .3s ease;
  transition: background .3s ease;
}
h3.v-collapse-toggler:hover {
  background-color: #c2b49a;
}
h3.v-collapse-toggler:after {
  content: "\F107";
  font-family: "Font Awesome 5 Free";
  float: right;
}
.v-collapse-content {
  max-height: 0;
  overflow: hidden;
  padding: 0 1.25rem;
  -webkit-transition: max-height .3s ease-out,padding .3s ease-out;
  transition: max-height .3s ease-out,padding .3s ease-out;
}
.v-collapse-content.v-collapse-content-end {
  padding: .5rem 1.25rem;
  max-height: 5000px;
  -webkit-transition: max-height .3s ease-in,padding .3s ease-in;
  transition: max-height .3s ease-in,padding .3s ease-in;
}
/*Fade*/
.fade-enter-active, .fade-leave-active {
